export {
    // From ~/Content/PeripheralConfigure
    createjscssfile,
    replacejscssfile,
    getPropertyNames,

    // content-web
    isString,
    isObject,
    isEmptyObject,
    getDefaultValue,
    confirmDependencyValid,
    confirmDependenciesValid,
    compareHexNumber,
};

// From createjscssfile()
function createjscssfile(filename, filetype) {
    var fileref;
    if (filetype === "js") { //if filename is a external JavaScript file
        fileref = document.createElement('script');
        fileref.setAttribute("type", "text/javascript");
        fileref.setAttribute("src", filename);
    }
    else if (filetype === "css") { //if filename is an external CSS file
        fileref = document.createElement("link");
        fileref.setAttribute("rel", "stylesheet");
        fileref.setAttribute("type", "text/css");
        fileref.setAttribute("href", filename);
    }
    return fileref;
};

// From replacejscssfile
function replacejscssfile(oldfilename, newfilename, filetype, callback) {
    var i,
        targetelement = (filetype === "js") ? "script" : (filetype === "css") ? "link" : "none", //determine element type to create nodelist using
        targetattr = (filetype === "js") ? "src" : (filetype === "css") ? "href" : "none", //determine corresponding attribute to test for
        allsuspects = window.document.getElementsByTagName(targetelement),
        newelement = null;

    if (typeof NUTOOL_PIN !== 'undefined' && filetype === "js") {
        // Add the preceding path
        oldfilename = "PeripheralConfigure/" + oldfilename;
        newfilename = "PeripheralConfigure/" + newfilename;
    }

    if (newfilename !== "PeripheralConfigure/") {
        newelement = createjscssfile(newfilename, filetype);
        if (typeof callback === 'function') {
            newelement.onload = function () {
                callback();
            }
        };
    }

    if (!g_bReadyForRelease && window.console) { window.console.log("In replacejscssfile, oldfilename:" + oldfilename + " / newfilename:" + newfilename); }

    // To update the data in js file fresh, remove the matched files and add the new one.
    for (i = allsuspects.length; i >= 0; i -= 1) { //search backwards within nodelist for matching elements to remove
        if (allsuspects[i] && allsuspects[i].getAttribute(targetattr) !== null) {
            if (allsuspects[i].getAttribute(targetattr) === oldfilename) {
                allsuspects[i].parentNode.removeChild(allsuspects[i]);
            }
            else if (allsuspects[i].getAttribute(targetattr) === newfilename && newfilename !== '') {
                allsuspects[i].parentNode.removeChild(allsuspects[i]);
            }
        }
    }
    if (newelement !== null) {
        allsuspects[0].parentNode.appendChild(newelement);
    }
}

// From getPropertyNames
function getPropertyNames(obj) {
    var moduleNames = [],
        index = 0,
        name;

    for (name in obj) {
        if (obj.hasOwnProperty(name)) {
            moduleNames[index] = name;
            index += 1;
        }
    }
    index = null;

    return moduleNames;
}

////////////////////////////////////////////////////////////////////////

function isString(object) {
    return typeof object === "string" ? true : false;
}

function isObject (object) {
    return typeof object === "object" ? true : false;
}

function isEmptyObject (object) {
    return isObject(object) ? object.length === 0 : false;
};


function getDefaultValue (data) {
    // Example: In M253/CLK.txt, if type is integer, but data can be 512 or [0].
    if(isObject(data)) {
        return data.length === 1 ? data[0] : data;
    }
    // Example: Handle type checkboxBoolean's data.
    else if(data === "true") {
        return true
    }
    else if(data === "false") {
        return false
    }
    else {
        return data;
    }
}

/**
 * Confirm one by one
 * @param {String} dependency Single target id
 * @param {String or Object} dependencyOption Its condition
 * @param {Array} local_perControls All peripheral controls
 * @returns {Boolean}  true: Meet the condition / false
 */
function confirmDependencyValid(dependency, dependencyOption, local_perControls) {
    function confirmDependencyOptionValid(dependencyOption, data) {
        let bMatch = false;
        dependencyOption = getDefaultValue(dependencyOption);
        data = getDefaultValue(data)
        if (String(data) === String(dependencyOption)) {
            bMatch = true;
        }
        // EX: [UART_INTEN_RDAIEN_Msk,UART_INTEN_THREIEN_Msk]
        else if(isObject(dependencyOption)) {
            bMatch = true;
            for(let key in dependencyOption){
                if(!data.includes(dependencyOption[key])){
                    bMatch = false;
                };
            }
        }
        else if(isString(dependencyOption)) {
            let bIncludesOr = dependencyOption.includes("|");
            let bIncludesAnd = dependencyOption.includes("&");
            // EX: ==512
            if (dependencyOption.startsWith("==")){
                bMatch = (data === dependencyOption.substring(2)) ? true : false;
            }
            // else if (dependencyOption.startsWith(">=")){
            //     bMatch = (data >= dependencyOption.substring(2)) ? true : false;
            // }
            // EX: <=16
            else if (dependencyOption.startsWith("<=")){
                bMatch = (data <= dependencyOption.substring(2)) ? true : false;
            }
            // EX: >16
            else if (dependencyOption.startsWith(">")){
                bMatch = (data > dependencyOption.substring(1)) ? true : false;
            }
            // EX: <512
            else if (dependencyOption.startsWith("<")){
                bMatch = (data < dependencyOption.substring(1)) ? true : false;
            }
            // else if (!bIncludesOr && bIncludesAnd) {
            //     dependencyOption = dependencyOption.split(/[&() ]/).filter(value => value !== "");
            //     bMatch = confirmDependencyOptionValid(dependencyOption, data);
            // }
            // EX: TIMER_ONESHOT_MODE|TIMER_PERIODIC_MODE|TIMER_CONTINUOUS_MODE;
            else if (bIncludesOr && !bIncludesAnd) {
                dependencyOption = dependencyOption.split(/[|() ]/).filter(value => value !== "");
                for(let value of dependencyOption){
                    if(confirmDependencyOptionValid(value, data)){
                        bMatch = true;
                    };
                }
            }
            // EX; (UART_INTEN_RDAIEN_Msk|UART_INTEN_THREIEN_Msk)&UART_INTEN_RLSIEN_Msk
            // else if (bIncludesOr && bIncludesAnd){
            //     bMatch = true;
            //     dependencyOption = dependencyOption.split("&");
            //     for(let value of dependencyOption){
            //         bMatch &= confirmDependencyOptionValid(value, data) 
            //     }
            // }
            else {
                // NOOP
            }
        }
        return bMatch;
    }

    let bMatch = false;
    let bNot = dependency.trim().startsWith("!");
    let tmpId = dependency.substring(bNot);

    for(var obj of local_perControls){
        if(tmpId === obj.id) {
            dependencyOption = dependencyOption[tmpId] || dependencyOption;
            bMatch = confirmDependencyOptionValid(dependencyOption, obj.data);
            // Based on id is unique.
            break;
        }
    }
    return Boolean(bNot^bMatch);
}

/**
 * Confirm this peripheral control's all dependencies
 * @param {String or Array} dependencies 
 * @param {String or Object} dependenciesOption 
 * @param {Array} local_perControls 
 * @returns {boolean} true: Show(Meet all conditions) / false: Hide
 */
function confirmDependenciesValid(dependencies, dependenciesOption, local_perControls) {
    let bShow = true;

    // EX: [CLK_PowerDownEnableRadio, CLK_PowerDownModeSelect]
    if (typeof dependencies === "object") {
        for(let value of dependencies){
            if(!confirmDependencyValid(value, dependenciesOption, local_perControls)){
                bShow = false;
            };
        }
    }
    else if (typeof dependencies === "string") {
        let bIncludesOr = dependencies.includes("|");
        let bIncludesAnd = dependencies.includes("&");

        // EX: UsePWM0CH0AdvanceBoolean&UsePWM0CH1AdvanceBoolean
        if (!bIncludesOr && bIncludesAnd) {
            // should redirect to case "typeof dependencies === object"
            dependencies = dependencies.split(/[&() ]/).filter(value => value !== "");
            bShow = confirmDependenciesValid(dependencies, dependenciesOption, local_perControls);
        }
        // EX: UsePWM0CH0AdvanceCapBoolean|UsePWM0CH1AdvanceCapBoolean
        // EX: (UART0TimeoutCounterCheckbox | UART0TimeoutWakeupSelect)
        else if (bIncludesOr && !bIncludesAnd) {
            bShow = false;
            dependencies = dependencies.split(/[|() ]/).filter(value => value !== "");
            for(let value of dependencies){
                if(confirmDependencyValid(value, dependenciesOption, local_perControls)){
                    bShow = true;
                };
            }
        }
        // EX: (UseADCCMP0Boolean|ADCOperationModeSelect)&BPWM0SSRCRadio
        // EX: PWM0CH01OutputFuncSetmode&(UsePWM0CH0ENBoolean|UsePWM0CH1ENBoolean)
        else if (bIncludesOr && bIncludesAnd){
            dependencies = dependencies.split("&");
            for(let value of dependencies){
                bShow &= confirmDependenciesValid(value, dependenciesOption, local_perControls) 
            }
        }
        // !bIncludesOr && !bIncludesAnd
        else if (dependencies !== "none" && dependencies !== "") {
            bShow = confirmDependencyValid(dependencies, dependenciesOption, local_perControls)
        }
        else {
            // NOOP
        }
    }
    else {
        // NOOP
    }
    return Boolean(bShow);
}

function compareHexNumber(val, max, min) {
    let _val = parseInt(val, 16);
    let _max = parseInt(max, 16);
    let _min = parseInt(min, 16);

    return (_val > _max) ? max : (_val < _min) ? min : val;
}